<template>
  <v-container>
    <v-row
      :align="'center'"
      :justify="'center'"
    >
      <v-stepper
        v-model="e1"
        style="min-width: 900px; max-width: 900px"
      >
        <v-stepper-header>
          <v-stepper-step
            @click="e1=1"
            step="1"
          >Общая информация</v-stepper-step>

          <v-divider />

          <v-stepper-step
            @click="e1=2"
            step="2"
          >Контакты</v-stepper-step>

          <v-divider />

          <v-stepper-step
            step="3"
            @click="e1=3"
          >Реквизиты</v-stepper-step>

          <v-divider />

          <v-stepper-step
            step="4"
            @click="e1=4"
          >Условия проживания</v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <v-row>
              <v-col
                class="pt-2 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Название"
                  v-model="name"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Юридическое название"
                  v-model="jurName"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Имя собственника"
                  v-model="benificareName"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Тип размещения"
                  v-model="type"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-btn
              color="primary"
              to="/"
            >
              Сохранить
            </v-btn>

            <v-btn
              text
              to="/"
            >Выход</v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-row>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Телефон"
                  v-model="phone"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="E-mail"
                  v-model="email"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Контактное лицо"
                  v-model="contactName"
                />
              </v-col>
            </v-row>

            <v-btn
              color="primary"
              to="/"
            >
              Сохранить
            </v-btn>

            <v-btn
              to="/"
              text
            >Выход</v-btn>
          </v-stepper-content>

          <v-stepper-content step="3">

            <v-row>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="ИП (ФИО)"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Адрес регистрации"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Почтовый адрес"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="БИК банка"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Телефон"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="E-mail"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="12"
                md="12"
              >
                <v-text-field
                  dense
                  label="Контактное лицо"
                />
              </v-col>
            </v-row>

            <v-btn
              color="primary"
              to="/"
            >
              Сохранить
            </v-btn>

            <v-btn
              text
              to="/"
            >Выход</v-btn>
          </v-stepper-content>
          <v-stepper-content step="4">
            <v-row>
              <v-col
                class="pt-2 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Стандартное время заезда"
                  v-model="stdTimeToIn"
                />
              </v-col>
              <v-col
                class="pt-2 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Стандартное время выезда"
                  v-model="stdTimeToOut"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Условия отмены бронирования"
                  v-model="cancelPerm"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  label="Минимальное количество дней"
                  v-model="minDays"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  v-model="pay"
                  label="Оплата, дней до заезда"
                />
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="6"
                md="6"
              >
                <v-text-field
                  dense
                  v-model="transfer"
                  label="Трансфер"
                />
