<script>
  export default {
    name: 'ObjectInformation',
    data () {
      return {
        e1: 1,
        jurName: null,
        benificareName: null,
        type: null,
        phone: null,
        email: null,
        contactName: null,
        stdTimeToIn: null,
        stdTimeToOut: null,
        cancelPerm: null,
        minDays: null,
        pay: null,
        transfer: null,
        name: null,
      }
    },
  }
</script>

<style scoped>
  .title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
